*{
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.form-check-input:checked {
  background-color: #15CDD4;
  border-color: #0F8F94;
}
.form-check{
  margin-left: auto;
  margin-right: 1rem;
}
.nav-bar{
  background-color: #0E8388;
  color: white;
  
}


ul{
  display: flex;
  gap: 3rem;
  margin-left: 1rem;
  align-items: center;
  height: 100%;

}
ul li{
  list-style: none;
  margin-block: 0.2rem;
}
.main-li{
  font-size: 1.4rem;
}

ul li:hover{
  color: #c5c7c5;
  cursor: pointer;
}
h1{
  /* text-align: center; */
  margin-block: 20px;
  font-size: 1.7rem;
}

form{
  margin-top: 70px;
  width: calc(100vw - 50px);
  margin-inline:auto ;
}


.btn{

  width: 10rem;
  background-color: #15CDD4;
  border: none;
  transition: all 0.3s linear;
}


.s-btn{
  width: 6rem;
}
.output{
  border: 0.4px solid #e6e9ec;
  width: 80.7%;
  margin-inline:auto ;
  border-radius: 4px;
}

.r-inp{
  width: 10rem;
  background-color: #fefefe;
}
.inp-container{
  background-color: #e9f8f4;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  flex-direction: column;
  width: 11.5rem;
  padding-top: 3px;
  border-radius: 4px;
  margin-left: 29.6%;
  box-shadow:1.5px 1px 5px rgba(0, 0, 0, 0.158);
  animation:hei 0.2s ease ;
  transition: all 0.2s ease;

}


@keyframes hei {
  0%{
    height: 0px;
    visibility: hidden;
  }
  100%{
    visibility: visible;
    height: auto;
  }
  
}
/* creating themes */
.nav-light{
  background-color: #0E888C;
  color: white;
}
.nav-dark{
  background-color: #095759;
  color: white;
}
.container-light{
  background-color: #ffffff8f;
  color: #011515;
}
.container-dark{
  background-color: #2C3333;
  color: #fafcfb;
}

.inp-container-light{
  background-color: #e9f8f4;
}
.inp-container-dark{
  background-color: #2E4F4F;
  color: #f5faf9;
  box-shadow:1.5px 1px 5px rgba(241, 241, 241, 0.158);
}
.btn-light{
  background-color: #15CDD4;
  color: #fbfcfd; 
}
.btn-dark{
  background-color: #0F8F94; 
}
.btn:hover{
  background-color: #108d92;
  color: #fbfcfd; 
}
.btn-dark:hover{
  background-color: #CBE4DE;
  color: #2E4F4F; 
  
}
.tbg-light{
  background-color: #fff;
  color: black;
}
.tbg-dark{
  background-color: #4a5351;
  color: rgb(113, 130, 129);
}
.tbg-dark:focus{
  background-color: #495250;
  color: rgb(255, 255, 255);
}
.color-s-light{
  background-color: #fbfcfd;
  color: black;
}
.color-s-dark{
  background-color: #2E4F4F;
  color: #f5faf9;
  box-shadow:1.5px 1px 5px rgb(241, 241, 241);
}
