@media only screen and (max-width: 600px) {
  nav,ul{
    display: flex;
    align-items: center;
    justify-content: center;
    
  }
  ul li,.form-check-label{
    margin-block-start:14px ;
  }
  .form-check-input{
    margin-block-start: 16px;
  }
  .form-check-label{
    font-size: 0.8rem;
  }
  .main-li{
    font-size: 1.1rem;
  }
  .item{
    display: none;
  }
}
@media only screen and (max-width: 415px) {


  .main-li{
    font-size: 0.9rem;
  }

}

.color-schame{
  margin-right: 1.5rem;
}
.Color-s-container{
  width: 160px;
  box-shadow: -1.5px 1px 5px rgba(0, 0, 0, 0.445);
  margin-left: auto;
  margin-right: 7px;
  margin-top: 3px;
  padding-block-end:1.3rem ;
  padding-block-start:0.7rem ;
  border-radius: 5px;


}
.color-scheme-heading{
  text-align: center;
  font-size: 1.2rem;
}
.scheme{
  width: 100%;
  display:flex;
  flex-direction: row;
  margin-bottom: 1rem;
}
.schemeName{
  margin-left: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.color-scheme-img{
  width: 37px;
  height: 37px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(1fr,3);
  border-radius: 6px;
  margin-left: auto;
  margin-right: 10px;

}
.color-scheme-img:hover,:focus{
  outline: 0.5px solid white;

}

.border-t-r{
  border-top-right-radius: 6px;
  border-top-left-radius: 6px;
}
.border-b-r{
  border-bottom-right-radius: 6px;
  border-bottom-left-radius: 6px;
}

/* setting color to scheme */
#img-1 .color-1{
  background-color: #FF0303;
}
#img-1 .color-2{
  background-color: #F9DBBB;
}
#img-1 .color-3{
  background-color: #4E6E81;
}
/* second color scheme */
#img-2 .color-1{
  background-color: #D8E9A8;
}
#img-2 .color-2{
  background-color: #4E9F3D;
}
#img-2 .color-3{
  background-color: #1E5128;
}
/* default color scheme */
#img-3 .color-1{
  background-color: #15cdd4;
}
#img-3 .color-2{
  background-color: aliceblue;
}
#img-3 .color-3{
  background-color: #0e888c;
}